.header {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;

  .title {
    font-size: 20px;
    text-align: center;
    line-height: 0.6rem;
    height: 0.6rem;
    background-color: white;
  }

  ul {
      display: flex;
      height: 50px;
      margin: 0;
      padding: 0;
      border-bottom: 1px solid #eee;
      background-color: #fff;
      list-style: none;

      li {
          display: flex;
          align-items: center;
          justify-content: center;
          flex: 1;

          a {
              display: block;

              color: #333;
              text-decoration: none;
              -webkit-tap-highlight-color:rgba(0,0,0,0);
          }
      }
  }

  p {
      color: #aeaeae;
      text-align: center;
  }
}
