@import '../../common/styles/base.scss';


.page {
   margin: 0.2rem;

   .form {
      & > div {
         width: 100%;
         font-size: 0;

         & > p {
            color: #666;
            font-size: 0.16rem;
         }

         & > input, textarea, select {
            width: 100%;
            height: 0.38rem;
            margin: 0;
            padding: 0 0.08rem;
            border:  1px solid cornflowerblue;
            box-sizing: border-box;
            box-shadow: none;
            outline:none;
            background: none;
            -webkit-tap-highlight-color:rgba(0,0,0,0);

            color: #666;
            font-size: 0.14rem;
         }

         & > textarea {
            height: 1.02rem;
            padding: 0.08rem;
         }
      }

      & > button {
         display: block;
         width: 100%;
         margin: 0.2rem 0;
         padding: 0.12rem 0.3rem;
         border: none;
         border-radius: 2px;
         background-color: cornflowerblue;

         color: aliceblue;
         font-size: 0.18rem;
         text-align: center;
      }
   }

   .preview {
      margin-top: 0.2rem;
      .title {
         display: flex;
         justify-content: space-between;
         line-height: 0;

         p {
            color: #666;
            font-size: 0.16rem;

            &:last-child {
               color: cornflowerblue;
            }
         }
      }


      pre {
         width: 100%;
         min-height: 1.6rem;
         padding: 0.12rem;
         background: #eee;
         overflow-x: scroll;
         box-sizing: border-box;

         font-size: 0.14rem;
      }
   }

   .cost {
      color: cornflowerblue;
      font-size: 0.14rem;
   }
}
